export default {
	data() {
		return {
			id:"",
			list:[],
		}
	},
	onLoad:function(option){
		var _self = this
		_self.id = option.id
		_self.getInfo()
	},
	methods: {
		getInfo:function(){
			var _self = this
			uni.request({
				url:_self.$api+"dockingManager/repairQuery",
				data:{id:_self.id,optionId:uni.getStorageSync("openId")},
				method:"GET",
				success:function(res){
					_self.list = res.data[0]
				}	
			})
		},
	}
}
